<template>
  <div class="header">
    <!-- logo -->
    <div class="logo-box">
      <router-link to="/">
        <img src="https://www.imooc.com/static/img/index/logo.png" height="72" alt="Logo">
      </router-link>
    </div>

    <!--  -->
    <m-nav v-if="navList.length" :list="navList" />

    <!--  -->
    <search />

    <!--  -->
    <login-area />
  </div>
</template>
<script>
import Search from 'components/search/search.vue'
import MNav from './header-nav.vue'
import LoginArea from './header-login.vue'
import { getHeader } from 'api/common.js'
import { ERR_OK } from 'api/config.js'
export default {
  data () {
    return {
      navList: {}
    }
  },
  mounted () {
    this.getHeaderData()
  },
  methods: {
    // 
    getHeaderData () {
      getHeader().then(res => {
        let { code, data } = res
        if (code === ERR_OK) {
          this.navList = data
        }
      }).catch(() => {
        this.navList = []
      })
    }
  },
  components: {
    MNav,
    Search,
    LoginArea
  }
}
</script>

<style lang="stylus" scoped>
  @import '~assets/stylus/variables.styl';
  .header
    z-index: 200;
    position: relative;
    padding-right: 10px;
    height: 100%;
    background-color: #fff;
    box-shadow: 0 4px 8px $shadow;
    .logo-box
      float: left;
      width: 140px;
      margin: 0 20px;
      height: 100%;
</style>
